import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import cn from 'classnames';
import { FaUserCircle } from 'react-icons/fa';
import { GiHamburgerMenu } from 'react-icons/gi';

const HeaderBlock = styled.div` 
    box-shadow:0px 1px 1px #eeeeee;
    padding:10px 0;
    position:fixed; top:0; left:0; width:100%; color:#fff;
    z-index:99999;
    .row { display:flex; justify-content:space-between; align-items:center; 
        h1 img {width:150px;}
        .nav {  
            .depth1 { display:flex;
                li { margin:0 30px; padding:20px 0; position:relative;
                    font-size:17px; 
                    .depth2 { display:none; color:#000;
                        position:absolute; top:100%; left:50%;
                        transform:translateX(-50%); 
                        width:165px; background:#fff; border:1px solid #000;
                        padding:20px 0px; margin-top:10px;
                        li { padding:5px 0px; font-size:15px; font-weight:normal; text-shadow:0px 0px 0px transparent; }
                    }
                    &:hover > .depth2 { display:block }
                }
            }
        }
        .mobNav { position:relative;
            button { padding:10px 20px; 
                border-radius:50px;
                font-size:20px; 
                background:#019CDC;
                color:#fff;
                display:flex; align-items:center;
                .ham { margin-right:10px }
            }
            .mobNavList { position:absolute; display:none;
                top:100%; right:0;
                width:150px;
                padding:10px 0px;
                background:#fff;
                color:#000;
                &.opener { display:block }
                ul { 
                    li { padding:0 20px; line-height:30px;
                        &:nth-child(3) {
                            border-top:1px solid #ddd;
                            padding-top:10px;
                            margin-top:10px;
                        }
                    }
                }
            }
        }
    }
`;

const Header = ({onSelect}) => {

    const [opener, setOpener] = useState(false);
    const openClose = () => {
        setOpener(!opener)
    }

    const [scy, setScy] = useState(0)
    const onScroll = () => {
        setScy(window.scrollY)
    }

    useEffect(
        ()=>{
            window.addEventListener("scroll", onScroll)
            return () => window.removeEventListener("scroll", onScroll)
        }, []
    )

    return (
        <HeaderBlock style={{ background: scy ? "#fff" : "transparent", color: scy ? "#000" : "#fff"  }}>
            <div className="row">
                <h1><Link to="/"><img src="./images/logo.png" alt="" /></Link></h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li><Link to="/product">예약</Link>
                            <ul className="depth2">
                                <li><Link to="/product" onClick={()=>onSelect(0)}>항공권예약</Link></li>
                                <li><Link to="/product" onClick={()=>onSelect(1)}>나의 예약</Link></li>
                                <li><Link to="/product" onClick={()=>onSelect(2)}>운항 정보</Link></li>
                                <li><Link to="/product" onClick={()=>onSelect(2)}>할인안내</Link></li>
                            </ul> 
                        </li>
                        <li><Link to="/news">서비스</Link>
                            <ul className="depth2">
                                <li>공항 서비스</li>
                                <li>기내 서비스</li>
                                <li>부가 서비스</li>
                            </ul>
                        </li>
                        <li><Link to="/community">혜택</Link>
                            <ul className="depth2">
                                <li>프로모션</li>
                                <li>액티비티</li>
                                <li>보딩패스 할인</li>
                                <li>차량 서비스</li>
                                <li>여행안심 서비스</li>
                            </ul>
                        </li>
                        <li><Link to="/about">나비포인트</Link>
                            <ul className="depth2">
                                <li>나의 나비포인트</li>
                                <li>적립안내</li>
                                <li>사용안내</li>
                            </ul>
                        </li>
                        <li><Link to="/about">지니스토어</Link>
                        </li>
                    </ul>
                </nav>
                <div className="mobNav">
                    <button onClick={openClose}>
                        <GiHamburgerMenu className="ham" />
                        <FaUserCircle />
                    </button>
                    <div className={cn("mobNavList", {opener})}>
                        <ul>
                            <li><Link to="/login">로그인</Link></li>
                            <li><Link to="/join">회원가입</Link></li>
                            <li><Link to="/product">예약</Link></li>
                            <li><Link to="/news">서비스</Link></li>
                            <li><Link to="/community">혜택</Link></li>
                            <li><Link to="/about">나비포인트</Link></li>
                            <li><Link to="/about">지니스토어</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </HeaderBlock>
    );
};

export default Header;