import React from 'react';
import styled from 'styled-components';

const FooterBlock = styled.div`
background:#F6F7F9;
padding:50px 0;
font-size:14px;
.row {
    .top span {
        font-weight:bold;
        &::after { content:'ㆍ'; }
        &:last-child::after { content:none; }
    }
    .address {padding:20px 0 0}
}
`;

const Footer = () => {
    return (
        <FooterBlock>
            <div className="row">
               <div className="top">
                    <span>회사안내</span>
                    <span>개인정보처리방침</span>
                    <span>약관 및 안내</span>
                    <span>고객서비스</span>
               </div>
                <p className="address">주소 : 서울시 강서구 공항대로 453 대표자 최정호, 사업자등록번호 121-81-89086 통신판매업 신고번호 : 제2008-서울 강서-0408</p>
                <p>Copyright © JIN AIR. All Rights Reserved.</p>
            </div>
        </FooterBlock>
    );
};

export default Footer;